import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Swiper, SwiperSlide } from 'swiper/react';
// Import Swiper styles
import 'swiper/css';
import 'swiper/css/pagination';
// import required modules
import { Autoplay, Navigation, Pagination } from 'swiper/modules';
import '../scss/shop.scss'
import { Button } from 'react-bootstrap';
import New from './New';
import Best from './Best';
import Etc from './Etc';
import Shoes from './Shoes';

const Shop = (props) => {
    const { all } = props
    const best = all.filter(item => item.category === 'best')
    const mdPick = all.filter(item => item.category === 'md_pick')
    const ETC = all.filter(item => item.category === 'ETC')
    const shoes = all.filter(item => item.category === 'shoes')
    const navigate = useNavigate()
    const [tab, setTab] = useState(0)
    const [active, setActive] = useState([true, false, false, false])

    console.log(active[tab])
    return (
        <div className="shop">

            {/*   1  */}
            <div className="banner1">
                <Swiper
                    spaceBetween={30}
                    centeredSlides={true}
                    autoplay={{
                        delay: 2500,
                        disableOnInteraction: false,
                    }}
                    pagination={{
                        clickable: true,
                    }}
                    navigation={true}
                    modules={[Autoplay, Pagination, Navigation]}
                    className="mySwiper"
                >
                    <SwiperSlide><img src={process.env.PUBLIC_URL+"/img/main1.jpg"} alt="" /></SwiperSlide>
                    <SwiperSlide><img src={process.env.PUBLIC_URL+"/img/main(1).jpg"} alt="" /></SwiperSlide>
                </Swiper>
            </div>

            <div className="tab_menu">
                <h2>SHOP</h2>
                <div className="tab">
                    <Button variant="outline-secondary" className={active[0]?"active":null} onClick={() => { setTab(0);
                        setActive([true, false, false, false])
                     }}>SHOP ALL</Button>
                    <Button variant="outline-secondary" className={active[1]?"active":null} onClick={() => { setTab(1);
                        setActive([false, true, false, false])
                     }}>CLOTHING </Button>
                    <Button variant="outline-secondary" className={active[2]?"active":null} onClick={() => { setTab(2);
                        setActive([false, false, true, false])
                     }}>ETC</Button>
                    <Button variant="outline-secondary" className={active[3]?"active":null} onClick={() => { setTab(3);
                        setActive([false, false, false, true])
                     }}>SHOES</Button>
                </div>

            </div>

            {
                [
                    <><New mdPick={mdPick}/><Best best={best}/><Etc ETC={ETC}/><Shoes shoes={shoes}/></>,
                    <><Best best={best}/><New mdPick={mdPick}/></>,
                    <Etc ETC={ETC}/>,
                    <Shoes shoes={shoes}/>
                ][tab]
            }

            <nav>
                <ul className="pagination pagination-sm justify-content-center">
                    <li className="page-item active" aria-current="page">
                        <span className="page-link">1</span>
                    </li>
                    <li className="page-item"><a className="page-link" href="#none">2</a></li>
                    <li className="page-item"><a className="page-link" href="#none">3</a></li>
                </ul>
            </nav>

        </div>
    );
}

export default Shop;
