import React from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
// Import Swiper styles
import 'swiper/css';
import 'swiper/css/pagination';
// import required modules
import { Autoplay, Navigation, Pagination } from 'swiper/modules';
import { useNavigate } from 'react-router-dom';

const Best = (props) => {
    const {best} = props
    const navigate = useNavigate()
    return (
        <div className="best_product">
            {/*   */}
            {/* <div className="title_box">
                  <h2>BEST</h2>
                  <div className="more"><span></span></div>
                </div> */}

            <Swiper
                slidesPerView={1}
                spaceBetween={10}
                pagination={{
                    clickable: true,
                }}
                breakpoints={{
                    640: {
                        slidesPerView: 2,
                        spaceBetween: 20,
                    },
                    768: {
                        slidesPerView: 4,
                        spaceBetween: 40,
                    },
                    1024: {
                        slidesPerView: 5,
                        spaceBetween: 50,
                    },
                }}
                modules={[Pagination]}
                className="mySwiper"
            >
                {
                    best.map((item, i) =>
                        <SwiperSlide onClick={()=>{navigate('/detail/' +  item.id)}}>
                            <img src={process.env.PUBLIC_URL + item.imgUrl} alt="" />
                            <div className="txt">
                                <h3>{item.title}</h3>
                                <span>{item.price}</span>
                                <i className="fa-solid fa-heart"></i>
                            </div>
                        </SwiperSlide>
                    )
                }
            </Swiper>
        </div>
    );
}

export default Best;
