import React, { useState } from 'react';
import { Breadcrumb, Button, Container, Table } from 'react-bootstrap';
import '../scss/cart.scss'
import { data, useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { addCount } from '../store';

const Cart = () => {
    let cart = useSelector((state) => state.cart)
    const navigate = useNavigate()
    const dispatch = useDispatch()

    return (
        <Container className='cart'>
            <div className="title text-center">
                {/* <Breadcrumb className='back'>
                    <Breadcrumb.Item onClick={() => { navigate('/') }}>Home</Breadcrumb.Item>
                    <Breadcrumb.Item onClick={() => { navigate('/cart') }}>Cart</Breadcrumb.Item>
                </Breadcrumb> */}
                <h4 className='mt-5'>CART</h4>

            </div>
            <Table className='mt-5 mb-5'>
                <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th className='count'> </th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    {
                        cart.map((v, i) =>
                            <tr key={i}>
                                <td><img src={process.env.PUBLIC_URL+v.img} alt="" /></td>
                                <td>{v.item}</td>
                                <td>{v.price}</td>
                                <td className='count'>{v.amount}<button onClick={()=>{dispatch(addCount(i))}}>+</button></td>
                                <td></td>
                            </tr>
                        )
                    }
                </tbody>
            </Table>
            <div className="btn mt-3">
                <Button variant="outline-secondary"></Button>
                <Button variant="danger"></Button>
            </div>

        </Container>
    );
}

export default Cart;