import React from 'react';
import { Button, Col, Container, Row, Tab, Tabs } from 'react-bootstrap';
import '../scss/detail.scss'
import { useNavigate, useParams } from 'react-router-dom';
import { addItem } from "./../store.js";
import { useDispatch } from 'react-redux';

const Detail = (props) => {
  const { all } = props;
  const { id } = useParams()
  const navigate=useNavigate()
  let dispatch = useDispatch()



  return (
    <>
    
    <Container className='detail'>
      <Row>
        <Col md={5}>
          <img src={process.env.PUBLIC_URL +all[id].imgUrl} alt="" className='img1' />
        </Col>
        <Col md={7} className='txt_box'>
          <h3>{all[id].title}</h3>
          <h4>{all[id].subTitle}</h4>
          <p className='price'>{all[id].price} (10% sael)</p>

          <div className='size'>
            <p>SIZE</p>
            <Button variant="danger">001</Button>
          </div>

          <div className='pick'>
            <p> </p>
            <img src={process.env.PUBLIC_URL +"/img/bag4.jpg"} alt="" />
            <img src={process.env.PUBLIC_URL +"/img/pr3.jpg"} alt="" />
            <img src={process.env.PUBLIC_URL +"/img/s5.jpg"} alt="" />
          </div>

          <div className="buy_box mt-4">
            <p> () :</p>
            <span> {all[id].price} (1)</span>
          </div>

          <div className="buy">
            <Button variant="outline-danger"  onClick={()=>{dispatch(addItem({img: all[id].imgUrl, price:all[id].price,  item: all[id].title, amount: 1 }))}}>ADD to CART</Button>
            <Button variant="outline-danger">BUY NOW</Button>
          </div>

        </Col>

      </Row>

    </Container>

    <Container className="detail_tab mt-5">
          <Tabs
            defaultActiveKey="profile"
            id="uncontrolled-tab-example"
            className="mb-3"
          >
            <Tab eventKey="home" title=" " >
              <img src={process.env.PUBLIC_URL +all[id].detail_} alt="" />
            </Tab>
            <Tab eventKey="profile" title="  " >
              <p className='notice mt-4'>
              <span>    </span> <br />
              -     7 , 
                      / . <br />
              -      . 
                    3,     30 <br />
              <br />
              <span>    </span> <br />
              -         . ,          <br />
              -       
              ( : , ,  ,    , LCD,      /   .) <br />
              -            ,       . <br />
              -            <br />
              -       <br />
              <br />
              (   1:1 E-MAIL   .)
              <br />
                  ,        . ( ,    )</p>
            </Tab>
            <Tab eventKey="contact" title=" " >
              <p className='text-center mt-5'>   .</p>
            </Tab>
          </Tabs>
          </Container>
    </>
  );
}

export default Detail;
